import { LitElement, html } from 'lit';
import { property } from 'lit/decorators.js';
import { prefix } from '../../globals/settings';
import FocusMixin from '../../globals/mixins/focus';
import styles from './status-indicator.scss';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import { green, gray, red, yellow, blue, purple  } from '@carbon/colors';

//icons import
import CheckmarkFilled16 from '@carbon/icons/lib/checkmark--filled/16';
import CloseFilled16 from '@carbon/icons/lib/close--filled/16';
import WarningAltFilled16 from '@carbon/icons/lib/warning--alt--filled/16';
import InProgress16 from '@carbon/icons/lib/in-progress/16';
import WarningFilled16 from '@carbon/icons/lib/warning--filled/16';
import PendingFilled16 from '@carbon/icons/lib/pending--filled/16';
import CircleDash16 from '@carbon/icons/lib/circle-dash/16';
import ConditionWaitPoint16 from '@carbon/icons/lib/condition--wait-point/16';


/**
 * Status indicator.
 *
 * @element cds-status-indicator
 */
@customElement(`${prefix}-status-indicator`)
class CDSTurnStatusIndicator  extends FocusMixin(LitElement) {
  /**
   * Can be one of: checkmark--filled-green, checkmark--filled-blue, close--filled, warning--alt--filled, in-progress, warning--filled, pending--filled, circle-dash, condition--wait-point, 
   */
  @property({ type: String }) statusIcon = "checkmark--filled-green";
  @property({ type: String }) statusText = "Ir aktÄ«vs";
  @property({ type: String }) date = "";

  renderStatusIcon(statusIcon: string): string {
    switch (statusIcon) {
        case "checkmark--filled-green":
          return CheckmarkFilled16({fill: green[50]});
        case "checkmark--filled-blue":
          return CheckmarkFilled16({fill: blue[70]});
        case "close--filled":
          return CloseFilled16({fill: red[60]});
        case "warning--alt--filled":
          return WarningAltFilled16({fill: yellow[30]});
        case "in-progress":
          return InProgress16({fill: blue[70]});
        case "warning--filled":
          return WarningFilled16({fill: red[60]});
        case "pending--filled":
          return PendingFilled16({fill: gray[60]});
        case "circle-dash":
          return CircleDash16({fill: blue[70]});
        case "condition--wait-point":
          return ConditionWaitPoint16({fill: purple[60]});
        default:
          return CheckmarkFilled16({fill: green[50]});
    }
  }
  render() {
    const{statusIcon, statusText, date, renderStatusIcon} = this;
    //set const hideDate true if date is empty
    const hideDate = date === '';

    return html`
      <div class="status-indicator">
        <div class="status-indicator__icon">${renderStatusIcon(statusIcon)}</div>
        <div class=status-indicator__content>
          <div class="status-indicator__status">${statusText}</div>
          ${!hideDate ? html`<div class="status-indicator__date">(${date})</div>` : ''}
        </div>
      </div>
    `;
  }

  static styles = styles; // `styles` here is a `CSSResult` generated by custom WebPack loader

}

export default CDSTurnStatusIndicator;
